import React from 'react';
import ToDoForm from './ToDoForm';
import Card from '../UI/Card';
import styles from './InputTodoItem.module.css';

const InputToDoItem = (props) => {
  const formDataHandler = (enteredData) => {
    const toDoData = {
      ...enteredData,
      id: Math.random().toString(),
    };
    props.onAddToDo(toDoData);
  };
  return (
    <Card className={styles.toDoStyle}>
      <div>
        <ToDoForm onSavingForm={formDataHandler} />
      </div>
    </Card>
  );
};

export default InputToDoItem;
