import React, { useState } from "react";
import { Link } from "react-router-dom";
import InputToDoItem from "./components/InputTodo/InputToDoItem";
import ToDos from "./components/ToDos/ToDos";
import styles from "./ToDo.module.css";
const DUMMY_TODOS = [];
function App() {
  const [toDo, setToDo] = useState(DUMMY_TODOS);
  const addToDoHandler = (toDoItem) => {
    setToDo((prevToDos) => {
      return [toDoItem, ...prevToDos];
    });
  };
  return (
    <div className={styles.toDoAppHeader}>
      <Link to="/dataListing">Detergent Listing</Link>
      <h1>To-Do App</h1>

      <InputToDoItem onAddToDo={addToDoHandler} />
      <ToDos updateItems={setToDo} items={toDo} />
    </div>
  );
}

export default App;
