import React, { useState } from 'react';
import Card from '../UI/Card';
import Modal from '../UI/Modal';
import messages from '../utils/messages.js';
import styles from './ToDoItem.module.css';

const ExpenseItem = (props) => {
  let prevTodo = props.items;
  const [title, _] = useState(props.title);
  const [showModal, setShowModal] = useState('none');
  const deleteHandler = (id) => {
    props.updateItems(prevTodo.filter((item) => item.id !== id));
    setShowModal('none');
  };
  const completeHandler = (id) => {
    props.updateItems(
      prevTodo.map((item) => {
        if (item.id === id) {
          return {
            ...item,
            completed: true,
          };
        }
        return item;
      })
    );
  };
  return (
    <div>
      {
        <Modal
          title={messages.title}
          message={messages.message}
          showModal={showModal}
          setShowModal={setShowModal}
          onConfirm={() => deleteHandler(props.id)}
        />
      }
      <Card className={styles.toDoItem}>
        <div className={styles.toDoItemDescription}>
          {props.completed ? (
            <h2>
              <s>{title}</s>
            </h2>
          ) : (
            <h2>{title}</h2>
          )} 
          <button
            onClick={() => {
              setShowModal('block');
            }}
          >
            <i class="fas fa-trash"></i>
          </button>
          <button onClick={() => completeHandler(props.id)}>
            <i class="fas fa-check"></i>
          </button>
        </div>
      </Card>
    </div>
  );
};
export default ExpenseItem;
